import { ImportSource, ImportType } from '@swiftread/server/src/import'
import { AppRouter } from '@swiftread/server/src/trpc'
import { TRPCClientError, createTRPCProxyClient } from '@trpc/client'
import { Auth } from '../../../apps/extension-legacy/spreed/src/non_app/background/auth'

// Define a generic type for TRPC clients with the same shape as AppRouter
type TRPCClientLikeAppRouter = ReturnType<
  typeof createTRPCProxyClient<AppRouter>
>

export async function importContent(
  trpcClient: TRPCClientLikeAppRouter,
  auth: Auth,
  data: string,
  importAttributes: {
    source: ImportSource
    importType: ImportType
    url?: string
    title?: string
  }
) {
  const session = await auth.session()
  if (!session) {
    throw new Error('No session found')
  }

  const supabase = auth.client
  const filePath = `${session.user.id}/${Date.now()}`
  console.log('[IMPORT] Uploading imported content to:', filePath)
  const { data: uploadData, error: uploadError } = await supabase.storage
    .from('imports')
    .upload(filePath, data)
  if (uploadError) {
    throw new Error('While uploading data to storage: ' + uploadError.message)
  }
  console.log('[IMPORT] Upload successful')

  // send request to app API to kick off content extraction from imported data
  let contentId: string | undefined = undefined
  try {
    const importedResponse = await trpcClient.import.imported.mutate({
      source: importAttributes.source,
      importsFile: uploadData.path,
      importType: importAttributes.importType,
      url: importAttributes.url,
      title: importAttributes.title,
    })
    contentId = importedResponse?.contentId
    console.log('[IMPORT] Imported content id: ', contentId)
  } catch (trpcError) {
    if (trpcError instanceof TRPCClientError) {
      console.error('TRPC client error: ', trpcError)
      throw new Error('While sending request to app API: ' + trpcError.message)
    } else {
      console.error('Unknown error: ', trpcError)
      throw trpcError
    }
  }
  return contentId
}

export enum UrlType {
  PdfReader = 'pdf-reader',
  EpubReader = 'epub-reader',
  ChatGPT = 'chat-gpt',
  KindleCloudReader = 'kindle-cloud-reader',
  GoogleDocs = 'google-docs',
  WebsiteStandard = 'website-standard',
}

export function getUrlType(urlString: string): UrlType {
  let urlType: UrlType
  try {
    const url = new URL(urlString)
    const domain = url.hostname
    if (domain.includes('.amazon.')) {
      urlType = UrlType.KindleCloudReader
    } else if (domain.includes('docs.google.com')) {
      urlType = UrlType.GoogleDocs
    } else if (domain.includes('chat.openai.')) {
      urlType = UrlType.ChatGPT
    } else if (
      urlString.includes('extension://') &&
      urlString.includes('pdf.js')
    ) {
      urlType = UrlType.PdfReader
    } else if (
      urlString.includes('extension://') &&
      urlString.includes('epub_reader')
    ) {
      urlType = UrlType.EpubReader
    } else {
      urlType = UrlType.WebsiteStandard
    }
  } catch (error) {
    console.warn(
      'Error parsing url string, assuming website standard: ',
      urlString,
      error
    )
    urlType = UrlType.WebsiteStandard
  }
  // console.log(`[URL] Type: ${urlType} for ${urlString}`)
  return urlType
}
